<template>
    <v-page>
        <div slot="header">
            <v-header>
                <v-btn></v-btn>
                <h1>收款网点</h1>
            </v-header>
        </div>
        <div class="rec-point-container">
            <div class="search-box">
                <i class="icon icon-search"></i>
                <div class="input-box">
                    <input type="text" placeholder="请输入收款网点" v-model="searchInfo.kw" @input="handleSearch">
                </div>
            </div>
            <div class="bank-list">
                <v-scroller :outTop="true" ref="recPointScroller">
                    <ul class="list-wrap">
                        <li class="item"
                            v-for="(item, index) in bankFilterList"
                            :key="index"
                            @click="jumpPage(item)"
                        >
                            <span class="bank">{{item.bankName}}</span>
                            <span class="bank-point">{{`(${item.bankPoint})`}}</span>
                        </li>
                    </ul>
                </v-scroller>
            </div>
        </div>
    </v-page>
</template>

<script>
    export default {
        name: 'recPoint',
        data () {
            return {
                // 搜索
                searchInfo: {
                    kw: ''
                },
                // 银行列表
                bankList: [
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '浦发银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '中国银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '农商银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '中国银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '浦发银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '中国银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '农商银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '中国银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    },
                    {
                        bankName: '东亚银行',
                        bankPoint: '上海张江支行'
                    }
                ],
                // 过滤后的数组
                bankFilterList: [],
                // 旧的kw
                oldKw: ''
            }
        },
        methods: {
            // 搜索
            handleSearch () {
                if (!this.searchInfo.kw) {
                    this.bankFilterList = this.bankList
                    return
                }
                this.bankFilterList = this.bankList.filter((item) => {
                    console.log(item)
                    return item.bankName.toString().indexOf(this.searchInfo.kw) > -1
                })
                this.$refs.recPointScroller.update()
            },
            // 初始化数据
            initBankList () {
                this.bankFilterList = this.bankList
            },
            // 跳转回输入页面
            jumpPage (item) {
                let obj = {
                    recPoint: item.bankName + item.bankPoint
                }
                window.T.back({
                    params: Object.assign(this.remittanceInfo, obj),
                    path: '/main/remittance/remittanceInput'
                })
            },
            // 获取页面传递参数
            getPageParams () {
                window.T.Native.getPageParams().then(({params}) => {
                    this.remittanceInfo = Object.assign(this.remittanceInfo || {}, params)
                })
            }
        },
        created () {
            this.initBankList()
            this.getPageParams()
        }
    }
</script>

<style lang="scss" scoped>
    .rec-point-container {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        .search-box {
            margin: px2rem(20) px2rem(28);
            height: px2rem(72);
            display: flex;
            align-items: center;
            padding: px2rem(14) px2rem(20);
            background-color: #ffffff;
            border-radius: px2rem(8);
            &:before {
                content: '';
                display: block;
            }
            .icon-search {
                width: px2rem(28);
                height: px2rem(28);
                background: url("./images/icon-search.png") no-repeat center;
                background-size: 100%;
                margin-right: px2rem(20);
                flex: none;
            }
            .input-box {
                flex: 1;
                input {
                    width: 100%;
                    border: none;
                    outline: none;
                    background-color: #ffffff;
                    font-size: px2rem(28);
                    &::-webkit-input-placeholder {
                        color: #9B9B9B;
                    }
                }
            }
        }
        .list-wrap {
            background-color: #ffffff;
            .item {
                height: px2rem(100);
                line-height: px2rem(100);
                color: $font-black;
                font-size: px2rem(32);
                padding: 0 px2rem(28);
                box-sizing: border-box;
                border-bottom: 1px solid #DBDBDB;
                @include bg(#ffffff);
                &:last-child {
                    border-bottom: none;
                }
            }
        }
    }
</style>



